import CryptoJs from 'crypto-js';
// 接口 数据加解密方法
// 加密
function JIAMI(value, key = makeKey()) {
  if (value === '') return '';
  let valueStr = CryptoJs.enc.Utf8.parse(value);
  let keyHex = CryptoJs.enc.Utf8.parse(key);
  let encrypted = CryptoJs.AES.encrypt(valueStr, keyHex, {
    mode: CryptoJs.mode.ECB,
    padding: CryptoJs.pad.Pkcs7,
  });
  return encrypted.toString();
}

// 解密
function JIEMI(value, key = makeKey()) {
  let keyHex = CryptoJs.enc.Utf8.parse(key);
  let decrypt = CryptoJs.AES.decrypt(value, keyHex, { mode: CryptoJs.mode.ECB, padding: CryptoJs.pad.Pkcs7 });
  let decryptedStr = decrypt.toString(CryptoJs.enc.Utf8);
  return decryptedStr.toString();
}
// 创建密钥
function makeKey() {
  let key = '';
  for (let index = 1; index < 16; index++) {
    if (index !== 1 && index !== 5 && index !== 7 && index !== 11) {
      key = index + key;
    }
  }
  return key;
}
export { JIAMI, JIEMI , makeKey};
