 import axios from 'axios';
 import { JIAMI, JIEMI } from './crypto';
 import { Dialog , Toast} from 'antd-mobile'

 axios.defaults.timeout = 60000;
 axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
 
 // body转换为search格式 ...?a=1&b=2
 axios.defaults.transformRequest = [
   data => {
     if (data && data.body) {
       let search = '';
       Object.keys(data).forEach(key => {
         search += encodeURIComponent(key) + '=' + encodeURIComponent(data[key]) + '&';
       });
       return search.slice(0, search.length - 1);
     }
     return data;
   },
 ];
 
 // 对异步请求进行计数
 // 并发请求时，当且仅当最后返回时触发Success Action
 let count = 0;

 const { REACT_APP_API_TYPE = null} = process.env
 const isEncrypt = (REACT_APP_API_TYPE === 'encrypt')  // 接口是否加密

 axios.interceptors.request.use(
   config => {
   
    if(!isEncrypt){
      // 不加密
      config.headers['my-super-requester'] = 'noencryption';
    }

    if (config.method === 'get') {
      config.params = {
        body: isEncrypt ? JIAMI(JSON.stringify(config.params)) : config.params,
      };
    }

    if (config.method === 'post') {
      config.data = {
        body: isEncrypt ? JIAMI(JSON.stringify(config.data)) : JSON.stringify(config.data),
      };
    }

     if (config.global !== false) {
      count = Math.max(count, 0);
      count = count + 1;
      if (count === 1) {
        Toast.show({
          icon: 'loading',
          duration: 0,
          content: '加载中…'
        })
      }
    }
     return config;
   },
   error => {
     count = -1;
     Dialog.alert({
      title: '温馨提示',
      content: '参数异常或格式不正确',
    })
     return Promise.reject(error);
   },
 );
 
 axios.interceptors.response.use(
   ({ config, data = { code: 500 } }) => {

     // 解密响应体
     if(isEncrypt){
      data = JSON.parse(JIEMI(data));
     }
     // 全局提示
     if (config.global !== false) {
       count = count - 1;
       if (count === 0) {
        Toast.clear()
       }
       if (data.code !== 200) {
         count = -1;
         Dialog.alert({
          title: '温馨提示',
          content: (data.data && data.data.message) || '服务器繁忙，请稍后再试',
        })
       }

      
     }
 
     // 失败
     if (data.code !== 200) {
       return Promise.reject(data);
     }
 
     return data;
   },
   error => {
     // 主动取消
     if (axios.isCancel(error)) {
       count = count - 1;
 
       if (count === 0) {
        Toast.clear()
       }
     }
 
     // 其他异常
     else {
       count = -1;
       Toast.clear()
       Dialog.alert({
        title: '非常抱歉',
        content: '网络异常或服务器宕机',
      })
     }
 
     return Promise.reject(error);
   },
 );
 
 const $ = {};
 
 $.get = (url, params = {}, config = {}) => {
   return axios.get(url, { params, ...config });
 };
 
 $.post = (url, params = {}, config = {}) => {
   return axios.post(url, params, config);
 };
 export default $;
 