import React, { useState } from 'react';
import { Button, List, Card, Slider, Tag, NoticeBar,Image } from 'antd-mobile';
import { hkReceivePolicy } from '@api/home';
import html2canvas from 'html2canvas';
import moment from 'moment'
import './index.less';
const tags = [
	{
		color: 'warning',
		name: '锦鲤附体',
	},
	{
		color: 'success',
		name: '喜鹊登枝',
	},
	{
		color: 'danger',
		name: '天降洪福',
	},
	{
		color: '#87d068',
		name: '搏彩人生',
	},
	{
		color: '#ff6430',
		name: '紫气东来',
	},
	{
		color: '#87d068',
		name: '马上发财',
	},
	{
		color: '#ff6430',
		name: '精彩十分',
	},
	{
		color: '#108ee9',
		name: '运气爆棚',
	},
	{
		color: '#2db7f5',
		name: '福至心灵',
	},
];
const Home = () => {
	const [ticketList, setTicketList] = useState([]);
	const [crtValue, setValue] = useState(5);
  const [imgUrl,setImgUrl] = useState('')
	// 生成一注
	const makeOneData = () => {
		const redList = makeArrByIndex(1, 33);
		const blueList = makeArrByIndex(1, 16);

		let sortedRedBalls = randomListByNums(redList, 6).sort((a, b) => {
			return Number(a) - Number(b);
		});
		return [...sortedRedBalls, ...randomListByNums(blueList, 1)];
	};
	// 生成指定注数的列表
	const makeListByNum = () => {
		let tickList = new Array(crtValue).fill([]);
		return tickList.map(() => {
			return makeOneData();
		});
	};
	// 生成指定开头结尾的递增列表
	const makeArrByIndex = (startIndex, endIndex) => {
		let arr = new Array(endIndex - startIndex + 1).fill(null);
		return arr.map((item, index) => {
			return startIndex + index;
		});
	};
	// 从某个列表中随机出指定个数的不重复元素
	const randomListByNums = (randomList, nums) => {
		let arr = [];
		while (nums > 0) {
			let currentNum = randomOne(randomList);
			if (!arr.includes(currentNum)) {
				nums--;
				arr.push(currentNum);
			}
		}
		return arr;
	};
	// 列表中随机一个元素
	const randomOne = (randomList) => {
		let length = randomList.length;
		let index = Math.floor(Math.random() * length);
		return ('0' + randomList[index]).slice(-2);
	};

	const makeList = () => {
		console.log(makeListByNum());
		setTicketList(makeListByNum());
	};
	const saveImg = () => {
		// 解决保存图片绘制不全的问题
		window.pageYOffset = 0;
		document.documentElement.scrollTop = 0;
		document.body.scrollTop = 0;

		let canvasELe = document.getElementsByClassName('canvas-box')[0];

		// html2canvas    保存图片的问题： 1.useCORS:true 2 。img添加 属性：crossOrigin="anonymous"  参考： https://www.jianshu.com/p/22bd5b98e38a
		html2canvas(canvasELe, { useCORS: true }).then((canvas) => {
      if(isInWeixinApp()){
        setImgUrl(canvas.toDataURL())
      }else{
        saveProveImg(canvas.toDataURL());
      }
     
		
		});
	};
  const isInWeixinApp = function() {
    return /MicroMessenger/.test(navigator.userAgent);
  };
	// 浏览器下载图片
	const saveProveImg = (base64Url) => {
		var link = document.createElement('a');
		link.href = base64Url; //下载链接
		link.setAttribute('download', '机选双色球.png');
		link.style.display = 'none';
		document.body.appendChild(link);
		link.click();
	};
  const demoLongText = '机选福彩双色球。好运爆棚，欧皇附体，冲冲冲～'
  const doAgain = () => {
    setImgUrl('')
    setTicketList([])
  }
	return (
		<div className='home'>
       <NoticeBar content={demoLongText} color='alert' />
			{ticketList.length > 0 && (
				<Card
					title='幸运结果'
					headerStyle={{
						color: '#1677ff',
					}}>
             {!imgUrl &&
					<><List className='canvas-box'>
              <List.Item className='time'> {moment().format("YYYY-MM-DD")}</List.Item>
              {ticketList.map((listItem, index) => {
                return (
                  <List.Item key={index}>
                    {listItem.map((ballList, ballIndex) => {
                      return (
                        <span key={index + '-' + ballIndex}>{ballList}</span>
                      );
                    })}
                  </List.Item>
                );
              })}{' '}
            </List><div className='card-content'>
                <div className='tag-box'>
                  {tags.map((tag, index) => {
                    return <Tag key={index} color={tag.color}>{tag.name}</Tag>;
                  })}
                </div>

                <Button color='danger' onClick={() => saveImg()}>
                  生成图片
                </Button>
              </div></>}

          {imgUrl && <div className='result-box'><Image src={imgUrl}></Image><div className='tips save-img-tip'>tips: 长按上方图片保存</div><Button color='danger' onClick={() => doAgain()}>
            重新选择
          </Button></div>
          }
				</Card>
			)}
			{ticketList.length === 0 && (
				<>
					<Card
						title='选择注数'
						headerStyle={{
							color: '#1677ff',
						}}>
						<Slider
							value={crtValue}
							ticks
							onChange={(value) => {
								setValue(value);
							}}
							min={1}
							max={20}
						/>
						<div className='tips'>tips: 拖动滑块选择注数</div>
						<div className='card-content'>
							<div className='cur-num'>
								当前选择的注数：<Tag  className='nums' color='danger'>{crtValue}注</Tag>{' '}
							</div>
							<div className='tag-box'>
								{tags.map((tag,index) => {
									return <Tag key={index} color={tag.color}>{tag.name}</Tag>;
								})}
							</div>
							<Button color='danger' onClick={() => makeList()}>
								开始机选
							</Button>
						</div>
					</Card>
				</>
			)}
		</div>
	);
};

export default Home;
